                      <div class="top">
             <span class="bt"> 动态总收益</span>
            <h2>{{ hmdInfo ? hmdInfo.info[5] : "0" | figureK }} USDT</h2>
         <!-- <button class="btn_no" @click="getReward">
                领 取
              </button> -->
          </div>

      <h1 class="textCenter title">
      <span>节点数据</span>
      </h1>
    <div class="cent">
          
          <div class="list">
            <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>VIP等级</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span
                  >VIP {{ hmdInfo ? hmdInfo.info[7] : "0" }}</span
                ></van-col
              >
            </van-row> -->
            <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>累计领导奖</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[9] : "0"  | figureK }} USDT</span></van-col
              >
            </van-row> -->
            
             <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>直推人数</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[11] : "0" }} 人</span></van-col
              >
            </van-row>
             <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>直推业绩</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[21] : "0"  | figureK }} H/s</span></van-col
              >
            </van-row>
             <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>网体人数</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[20] : "0"  }} 人</span></van-col
              >
            </van-row>
             <van-row type="flex" justify="space-between" >
              <van-col span="12" class="textLeft"
                ><span>网体业绩</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[22] : "0"  | figure }} H/s</span></van-col
              >
            </van-row>
          </div>

    </div>
             <h1 class="textCenter title">
      <span>合约数据</span>
      </h1>
    <div class="cent" >
          
          <div class="list">
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>HROD合约地址</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span
                  > {{
                  hmdInfo ? protectAddress(this.ArrList.FiveArr,8) : "--"
                }}</span
                ></van-col
              >
            </van-row>
            <van-row type="flex" justify="space-between" >
              <van-col span="12" class="textLeft"
                ><span>我的邀请地址</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span style="width:100%;white-space">{{ this.link }}</span></van-col
              >
            </van-row>
          </div>

    </div>
     <button class="btn_no" 
      v-clipboard:copy="link"
                v-clipboard:success="onCopy"
                v-clipboard:error="onError"
     >
                复制邀请链接
              </button>

  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
      //   currency: "",
        link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
        protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
        getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
      // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
     getReward: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .getBonusReward()
        .send({ from: account })
        .then((res) => {
          console.log("getBonusReward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getMiningInfoFun: function (num) {
      var _this = this;
      _this.tabsNum = num;
      _this.mxtContract.methods
        .getMiningInfo(_this.account)
        .call()
        .then((result) => {
          console.log(result.ct[11 + num]);
          _this.F5data.allBonus = result.ct[11 + num];
          _this.F5data.balanceBonus = result.ct[7 + num];
          _this.F5data.vipLength = result.ct[15 + num];
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          // this.getMiningInfoFun(1);
             if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
