      color: #fff;
    }
  }
  // .cent::before{
  //     content: '';
  //     display: inline-block;
  //     position: absolute;
  //     width: 59px;
  //     height: 85px;
  //     background: url(../../assets/img/info/1.png) top center no-repeat;
  // background-size: 100%;
  // top: 3%;
  // left: 0;
  // }
  //     .cent::after{
  //     content: '';
  //     display: inline-block;
  //     position: absolute;
  //     width: 59px;
  //     height: 85px;
  //     background: url(../../assets/img/info/2.png) top center no-repeat;
  // background-size: 100%;
  // top: 37%;
  // right: 0;
  // }
  // .qiu{position: absolute;left: 10%;}
  .cent {
     background: #0D202E;
    border-radius: 20px;
