            padding: 12px 0;
          }
          .textRight span {
            font-size: 15px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 600;
            color: #fff;
            overflow: hidden;
            text-overflow:ellipsis;
          }
        }
    span {
      font-size: 12px;
