// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueI18n from 'vue-i18n'
import * as filters from './filters/filters.js'


import LangEn from '../static/lang/en'
import LangZhCHS from '../static/lang/zhCHS'
import LangZhCHT from '../static/lang/zhCHT'


import store from "./store";

import Vant from 'vant';
import 'vant/lib/index.css';

  


import VueClipboard from 'vue-clipboard2';

Vue.use(VueClipboard)
Vue.use(Vant);
Vue.use(Vant);
Vue.prototype.ArrList={
    dappArr:"0x6755790448b2bdd7adecc3c18195678ee7560329",
    UsdtArr:"0x6e42eAE3317a19Eb4B5115B0bcE388BBf21CB917",
    FiveArr:"0x0380a8ab59180a8648ad6c4f145f072a2f669bbb",
    GtArr:"0x9A829D93b956193Bb3c28182e72d1052f3ec4893",
    AesArr:"0x6485accb893eac4f4de15b6ea7c1192f96db88ca",
    XhArr:"0x000000000000000000000000000000000000dead",
    AesLpArr:"0xb56b83b84dbc2a3d7de4bf88969b20907115000a",
}

Vue.config.productionTip = false;

Object.keys(filters).forEach(key=>{
    Vue.filter(key,filters[key])//插入过滤器名和对应方法
})

Vue.use(VueI18n);



const i18n = new VueI18n({
    locale: 'zhCHS', // 语言标识
    messages: {
        'en': LangEn,
        'zhCHS': LangZhCHS,
        'zhCHT': LangZhCHT
    }
});



/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: { App }
});


