/* 时间转换器 yyyy-mm-dd hh:ii:ss */
export function setTime(time){
	try{
		var now = new Date(time.replace(/-/g, '/'));
		var year = now.getFullYear();
		var month = (now.getMonth() + 1) < 10 ? '0' + (now.getMonth() + 1) : (now.getMonth() + 1);
		var date = now.getDate() < 10 ? '0' + now.getDate() : now.getDate()
		var hour = now.getHours() < 10 ? '0' + now.getHours() : now.getHours()
		var minute = now.getMinutes() < 10 ? '0' + now.getMinutes() : now.getMinutes()
		var second = now.getSeconds() < 10 ? '0' + now.getSeconds() : now.getSeconds()
		return year + "-" + month + "-" + date + " " + hour + ":" + minute + ":" + second;       
	}catch(e){
		return '';
	}
}

/* 百分比转换 */
export function percent(num){
	let new_num = num || 0
	let all = (new_num*10000 / 100).toFixed(4)
	return all+'%';
}

/* 单价保留两位小数 */
export function price(num){
	let new_num = 0;
	if(num){
		new_num = parseFloat(num).toFixed(4) || 0.00
	}else{
		new_num = 0;
	}
	return new_num;
}
/* 单价保留两位小数 */
export function price4(num){
	let new_num = Number(num / 10**18).toFixed(4) || 0.00
	return new_num;
}

/* 九位数 */
export function priceFive(num){
	let new_num = Number(num / 10**9).toFixed(4) || 0.00
	return new_num;
}


/* 单价保留两位小数 */
export function figureW(num){
	var value=Number(num / 10**18)
	if (isNaN(value)) {
		return "--"
	}
	var arr = value.toString().split(".");
	var numStr = arr[0].length;
	var realVal = 0;
	   if (numStr > 4) {
		   realVal = parseFloat(Number(value / 10000) ).toFixed(2) + 'w'
	   }else{
		   realVal = parseFloat(value).toFixed(2);
	   }
   
	return realVal
}
/* 单价保留两位小数 */
export function figureK(num){
	var value=Number(num / 10**18)
	if (isNaN(value)) {
		return "--"
	}
	var arr = value.toString().split(".");
	var numStr = arr[0].length;
	var realVal = 0;
	   if (numStr > 5) {
		   realVal = parseFloat(Number(value / 1000) ).toFixed(4) + 'K'
	   }else{
		   realVal = parseFloat(value).toFixed(4);
	   }
   
	return realVal
}

