/* 单价保留两位小数 */
export function figure(num){
	let new_num = Number(num / 10**18).toFixed(4) || 0.00
	return new_num;
}

/* 单价保留两位小数 */
export function figure10(num){
	num = (num / 10**10).toString();
	let index = num.indexOf('.');
	if (index !== -1) {
		num = num.substring(0, 6 + index + 1)
	} else {
		num = num.substring(0)
	}
	return parseFloat(num).toFixed(6)
}

/* 手机号码显示前三位 */
export function phoneStr(str){
	let phone = str.substring(0,3)+'********';
	return phone;
}

/* 手机号码中间四位* */
export function phoneStrTwo(str){
	let phone ="188****1234"
	if(str){
		phone = str.substring(0,3)+'*****'+str.substr(str.length-4) ;
	}
	
	return phone;
}

/* 日期截取2* */
export function dateFormatTwo(str){
	 let arr= str.split(" ");
	 let arrA= arr[0].split("-");
	let time = arrA[1]+'-'+arrA[2]+' '+arr[1];
	return time;
}

/* 日期截取* */
export function dateFormat(str){
	 let arr= str.split(" ");
	 let arrA= arr[0].split("-");
	let day = arrA[1]+'-'+arrA[2] ;
	return day;
}

/* 数字转换成汉字 */
export function numChange(num){
	let strNum = num.toString(); 
	let val = '';
	switch(strNum){
		case '1':val='一';break;
		case '2':val='二';break;
		case '3':val='三';break;
		case '4':val='四';break;
		case '5':val='五';break;
		case '6':val='六';break;
		case '7':val='七';break;
		case '8':val='八';break;
		case '9':val='九';break;
		case '10':val='十';break;
		default:val='';
	}
	return val;
}

/* 157 8888 8888 格式转化 */
export function phoneFormat(phone){
	return phone.replace(/^(.{3})(.*)(.{4})$/, '$1 $2 $3')
}



/* 字符串截取两位* */
export function toFixedTwo(str){
	 let arr= parseFloat(str).toFixed(2);
	return arr;
}



/* 字符串截取四位* */
export function toFixedFour(num){
	num = num.toString();
	let index = num.indexOf('.');
	if (index !== -1) {
		num = num.substring(0, 6 + index + 1)
	} else {
		num = num.substring(0)
	}
	return parseFloat(num).toFixed(6)
}
/* 字符串截取四位* */
export function toFixed10(num){
	num = (num / 10**18).toString();
	let index = num.indexOf('.');
	if (index !== -1) {
		num = num.substring(0, 10 + index + 1)
	} else {
		num = num.substring(0)
	}
	return parseFloat(num).toFixed(10)
}

/* 转化时间 yy/dd/mm */
export function timeFormat(str){
	let time = new Date(str.replace(/-/g,'/')).Format('yy/MM/dd')
	return time;
}


