<template>
  <div class="info">
    <HeadTop :lang="lang"  @changeAppLocale="changeAppLocale" :page1="'info'"  :titleHead="this.$t('message.f_a_2')"/>


    <h1 class="textCenter title">
       <span>{{ $t("message.f_15") }}{{ $t("message.f_16") }}</span> 
      </h1>
    <div class="cent">
      <div class="my">
        <van-row type="flex" justify="space-between" gutter="20" >
          <van-col span="12">
            <span>{{ $t("message.f_19") }}</span>
            <h3>{{ hmdInfo ? hmdInfo.info[2] : "0" | figureK }} H/s</h3>
          </van-col>
          <van-col span="12">
            <span>{{ $t("message.f_20") }}</span>
            <h3>{{ hmdInfo ? hmdInfo.info[23] : "0" | figureK }} H/s</h3>
          </van-col>
          <van-col span="12">
            <span>距离出局还差（USDT）</span>
            <h3>{{ hmdInfo ? (Number(hmdInfo.info[6])-Number(hmdInfo.info[4])-Number(hmdInfo.info[5])-Number(hmdInfo.info[9])) : "0" | figureK }} </h3>
          </van-col>
          <van-col span="12">
            <span>累计HROD收益</span>
            <h3>{{ hmdInfo ? (Number(hmdInfo.info[5])*hmdInfo.UsdtToHroddPrice)  : "0" | figureK}} </h3>
          </van-col>
          <van-col span="12">
            <span>累计USDT收益</span>
            <h3>{{ hmdInfo ? hmdInfo.info[4] : "0" | figureK }} </h3>
          </van-col>
          
          <van-col span="12">
            <span>累计领导奖励（USDT）</span>
            <h3>{{ hmdInfo ? hmdInfo.info[8] : "0" | figureK }} </h3>
          </van-col>
           <van-col span="12">
            <span>我的HROD销毁总量</span>
            <h3>{{ hmdInfo ? hmdInfo.info[17] : "0" | figureK  }}  <br/> <small>≈
              {{ hmdInfo ? (Number(hmdInfo.info[17])*hmdInfo.HrodToUsdtPrice): "0" | figureK }}USDT</small></h3>
          </van-col>
          <van-col span="12">
            <span>我的ROD销毁总量</span>
            <h3>{{ hmdInfo ? hmdInfo.info[16] : "0" | figureK  }}  <br/> <small>≈
              {{ hmdInfo ? (Number(hmdInfo.info[16])*hmdInfo.RodToUsdtPrice): "0" | figureK }}USDT</small></h3>
          </van-col>
        </van-row>
      </div>

      <img src="../../assets/img/info/3.png" alt="" width="100%"/>

      <div class="ming" >
        <div class="ming_bix">
          <van-row type="flex" justify="space-between" class="line" gutter="20" align="center" >
            <van-col span="12"
              ><span>静态收益（USDT）</span>
              <h3> {{ hmdInfo ? hmdInfo.info[10] : "0" | figure }}  <br/><small>≈{{ hmdInfo ? (Number(hmdInfo.info[10]))*hmdInfo.UsdtToHroddPrice : "0" | figure }} HROD</small></h3>
              </van-col>
            <van-col span="12" v-if="hmdInfo && hmdInfo.info[10]=='0'" class="textRight">
              <button class="btn" >
                {{ $t("message.f_28") }}
              </button></van-col
            >
             <van-col span="12" v-else class="textRight">
              <button class="btn_no" @click="getReward">
                {{ $t("message.f_28") }}
              </button></van-col
            >
          </van-row>
          <van-row type="flex" justify="space-between" class="line" align="center">
            <van-col span="12"
              ><span>动态收益（USDT）</span>
              <h3>{{ hmdInfo ? hmdInfo.info[20] : "0" | figure }}</h3></van-col
            >
             <van-col span="12" v-if="hmdInfo && hmdInfo.info[20]=='0'" class="textRight">
              <button class="btn" >
                {{ $t("message.f_28") }}
              </button></van-col
            >
            <van-col span="12" v-else class="textRight">
              <button class="btn_no" @click="getReward">
                {{ $t("message.f_28") }}
              </button></van-col
            >
          </van-row>
          <van-row type="flex" justify="space-between" class="line" align="center">
            <van-col span="12"
               ><span>领导奖励（USDT）</span>
              <h3>{{ hmdInfo ? hmdInfo.info[7] : "0" | priceFive }}</h3></van-col
            >
            <van-col span="12" v-if="hmdInfo && hmdInfo.info[7]=='0'" class="textRight">
              <button class="btn" >
                {{ $t("message.f_28") }}
              </button></van-col
            >
            <van-col span="12" v-else class="textRight">
              <button class="btn_no" @click="getReward">
                {{ $t("message.f_28") }}
              </button>
              </van-col
            >
          </van-row>
          <!-- <van-row type="flex" justify="space-between">
            <van-col span="12"
              >
              <span>节点ALE收益</span>
              <h3>{{ hmdInfo ? hmdInfo.info[37] : "0" | figure }}</h3></van-col
            >
            <van-col span="12" v-if="hmdInfo && hmdInfo.info[37]=='0'" class="textRight">
              <button class="btn_no" >
                {{ $t("message.f_28") }}
              </button></van-col
            >
            <van-col span="12" v-else class="textRight">
              <button class="btn" @click="withdrawBNBfun">
                {{ $t("message.f_28") }}
              </button></van-col
            >
          </van-row> -->
        </div>
      </div>

    </div>

  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account","aesBalance"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract","add_hmdCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
     changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getUsdt(num){
      return num*this.hmdInfo.fivePrice;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    getPrice() {
       if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      this.mxtContract.methods
        .getTokenPrice(
          "0x0496c8f51b6e6500bdd6e3bc0664857e976c6bc6",
          "0x6e42eae3317a19eb4b5115b0bce388bbf21cb917",
          "1000000000000000000"
        )
        .call()
        .then((res) => {
          // console.log('shdjsh')
          this.TokenPrice = parseFloat(
            web3.utils.fromWei(res, "ether")
          ).toFixed(4);
          console.log("getTokenPrice======", web3.utils.fromWei(res, "ether"));
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      console.log(111)
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
          console.log("withdrawProfit", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

    withdrawFIVEfun: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var vipAvgBonus = web3.utils.fromWei(_this.hmdInfo.info[37], "ether");
      //   if (vipAvgBonus <= 0) {
      //     this.$toast(this.$t("message.bonusMsg"));
      //     return;
      //   }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .withdrawFive()
        .send({ from: account })
        .then((res) => {
          console.log("withdrawBNB", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          // _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //领取分红奖励
    getvipDividendFun: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var vipAvgBonus = web3.utils.fromWei(_this.hmdInfo.info[20], "ether");
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .getBonus()
        .send({ from: account })
        .then((res) => {
          console.log("getvipDividendFun", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          // _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_hmdCoinContract();
    };
    var getDataCalculate = () => {
        getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          _this.dataInfo = _this.hmdInfo.info;
            // _this.getPrice();
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
           clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 10000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background: url(../../assets/img/info/bg1.png) top center no-repeat;
  background-size: 100%, 100%;
  padding-top: 5em;
  padding-bottom: 40px;
  .top {
    text-align: center;
        padding: 20px 30px 20px;
    width: 90%;
    box-sizing: border-box;
    background: url(../../assets/img/info/2.png) top center no-repeat;
        background-size: 100% 100%;
    margin: 25px auto 20px;
    box-sizing: border-box;
    .sl {
      margin-bottom: 15px;
      text-align: center;
    }
    span {
      color: #2DF0AE;
      font-family: PingFangSC-Regular, PingFang SC;
      font-size: 12px;
      display: block;
      text-align: center;
    }
    h3{
  color: #FFCD51;
  

    }
    h2 {
      font-size: 18px;
      margin: 4px 0;
      // margin-bottom: 12px;
      font-family: DINAlternate-Bold, DINAlternate;
      font-weight: bold;
      color: #ffffff;
      text-align: center;
    }
  }
  
.btn{
  width: 80px;
  text-align: center;
  color: #000;
  padding: 7px;
  border-radius:30px;
  border:none;
  background: linear-gradient(#858585,#b3b3b3,#e4e4e4);
}
.btn_no{
  width: 80px;
  text-align: center;
  color: #000;
  padding: 7px;
  border-radius:30px;
  border:none;
  background: linear-gradient(#ffb237,#ffcd51,#ffe76a);
}
.btn_on_2{
  width:50%;
  display: block;
  margin:30px auto;
  text-align: center;
  color: #000;
  padding: 10px;
  border-radius:30px;
  border:none;
  font-weight: bold;
  background: linear-gradient(#ffb237,#ffcd51,#ffe76a);
}
      .title {
    margin: 0px 0 20px;
    span{
      position: relative;
      font-size: 20px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #FFB237;
    line-height: 39px;
    margin: 0 0 30px;
    }
  }
   .title span::before{
      content: '';
      display: inline-block;
      position: absolute;
      width: 10px;
      height: 15px;
      background: url(../../assets/img/info/1.png) top center no-repeat;
  background-size: 100%;
  top: 8px;
  left:-25px;
  }
  .title span::after{
      content: '';
      display: inline-block;
      position: absolute;
      width:10px;
      height: 15px;
      background: url(../../assets/img/info/1.png) top center no-repeat;
  background-size: 100%;
  top:8px;
  right: -25px;
  }
  
  .cent {
    // background: url(../../assets/img/info/bg.png) top center no-repeat;
    background: #0D202E;
    border-radius: 10px;
    width: 90%;
    margin:0 auto 40px;
     border-bottom:6px solid #153247;
    box-shadow: 0px 1px 10px #1b3b52;
    span {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #2DF0AE;
      
    }
    h3 {
      font-size: 18px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #FFFFFF;
      margin-bottom: 25px;
      font-weight: bold;
      margin-top:8px;
      overflow: hidden; /*溢出隐藏*/
      white-space: nowrap; /*规定文本不进行换行*/
      text-overflow: ellipsis;
      width: 100%;
      // margin-right: 5px;
      small{
        color:#FFCD51;
      }
    }
    .my {
      // background: linear-gradient(#fff3f5, #ffffff);
      // box-shadow: 0 7px 14px #77acf7;
      // border-radius: 30px;
      padding: 30px 15px 10px;
      box-sizing: border-box;
      text-align: center;
      position: relative;
      z-index: 2;
       h3{
          text-align: center;
        }
      span{
        display: block;
        text-align: center;
       
      }
    }
    .ming{
      padding:20px 10px;
      .line{
          border-radius: 10px;
          background: #0A1923;
          padding: 15px;
          margin: 15px 0;
          h3{margin-bottom: 0px !important;}
      }
    }
      .line{
      border-bottom:0.5px solid #000000;
    } 
         .list {
          // background: #fff;
          border-radius: 0 0 15px 15px;
          padding:10px 20px;
          span {
            display: inline-block;
            padding:12px 0;
          }
          .textRight span {
            font-size: 15px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 600;
            color: #fff;
            overflow: hidden;
            text-overflow:ellipsis;
          }
        }
  }
}
</style>



