/*openBannerAD 是否打开banner广告
 * @param {Boolen} isOpen
 * */
export function openBannerAD(isOpen) {
	console.log('banner广告======', isOpen)
	//安卓交互
	if (getUa() == 'az') {
		try {
			window.android.browseBannerAds(isOpen);
		} catch (e) {
			showToast(e, 'none')
		}
	}
	//ios交互
	else if (getUa() == 'ios') {
		try {
			var obj = {
				'isWatch': isOpen
			}
			window.webkit.messageHandlers.jsCallOCBrowseBannerAdsAction.postMessage(obj);
			// window.jsCallOCBrowseBannerAdsAction(obj);
		} catch (e) {
			showToast(e, 'none')
		}
	}
}
/* 原生交互 保存图片 */
export function saveSharePicture(imagebase64){
	//ios
	if(getUa() == 'ios'){
		window.webkit.messageHandlers.jsCallOCSaveShareAction.postMessage(imagebase64);
	}
	//安卓
	else if(getUa() == 'az'){
		let imagebase642 = imagebase64.replace('data:image/png;base64,', '');
		window.android.saveSharePicture(imagebase642);				 
	}
	//其他机型
	else{
		saveFile(imagebase64, new Date().getTime())
	}
}
/* h5保存图片 */
export function	saveFile(imagebase64, filename) {
	const save_link = document.createElementNS('http://www.w3.org/1999/xhtml', 'a');
	save_link.href = imagebase64;
	save_link.download = filename;
	const event = document.createEvent('MouseEvents');
	event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
	save_link.dispatchEvent(event);  		    
}
