/*手机号校验*/
export function phoneCheck(phone) {
	if (!(/^1[3|4|5|7|8]\d{9}$/.test(phone))) {
		return false;
	} else {
		return true;
	}
}

/* 只允许输入数字正则 */
export function numberCheck(str) {
	if ((/[^\d]/g).test(str)) {
		return false
	}else {
		return true
	}
}

