/*
 * string time 2020/11/14 20:20
 * int millisecond 毫秒
 */
export function setTime(time, millisecond) {
	var thisMill = new Date(time).getTime();
	var newMill = thisMill + millisecond;
	var date = new Date(newMill);
	var year = date.getFullYear(); //获取当前年份
	var mon = (date.getMonth() + 1) < 0 ? '0' + date.getMonth() + 1 : date.getMonth() + 1; //获取当前月份
	var da = (date.getDate()) < 0 ? '0' + date.getDate() : date.getDate(); //获取当前日
	var h = (date.getHours()) < 0 ? '0' + date.getHours() : date.getHours(); //获取小时
	var m = (date.getMinutes()) < 0 ? '0' + date.getMinutes() : date.getMinutes(); //获取分钟
	var s = (date.getSeconds() < 0) ? '0' + date.getSeconds() : date.getSeconds(); //获取秒
	return year + '-' + mon + '-' + da + ' ' + h + ':' + m + ':' + s;
}





