import Web3 from 'web3'
var getWeb3 = null;


/**
 * 初始化
 * @param {Object} callback 返回账户地址
 */
async function Init(contractABI, contractAddress, callback) {
        if (window.ethereum) {
            
            window.web3 = new Web3(window.ethereum)
            getWeb3 = new Web3(window.ethereum)
        //     let contract = null;
        //     contract = new getWeb3.eth.Contract(contractABI, contractAddress);
        //     web3.eth.getAccounts().then(console.log);
        //   return
            await window.ethereum.enable().then(function (accounts) {
                let contract = null;
                web3.eth.net.getId().then((id) => {
                    if (id == 56) {
                        contract = new getWeb3.eth.Contract(contractABI, contractAddress);
                        getWeb3.eth.getBalance(accounts[0], function (err, res) {
                            if (!err) {
                                
                                callback(contract, accounts[0], res);
                            } else {
                                console.log(err);
                            }
                        })
                    } 
                });
            })
        } else if (window.web3) {
            window.web3 = new Web3(window.web3.currentProvider)
            getWeb3 = window.web3;
            
            await window.ethereum.enable().then(function (accounts) {
                let contract = null;
                web3.eth.net.getId().then((id) => {
                    if (id == 56) {
                        contract = new getWeb3.eth.Contract(contractABI, contractAddress);
                        getWeb3.eth.getBalance(accounts[0], function (err, res) {
                            if (!err) {
                                
                                callback(contract, accounts[0], res);
                            } else {
                                console.log(err);
                            }
                        })
                    } 
                });
            })

        } else {
            window.web3 = new Web3('https://exchaintestrpc.okex.org')
            getWeb3 = window.web3;
            await window.ethereum.enable().then(function (accounts) {
                let contract = null;
                web3.eth.net.getId().then((id) => {
                    if (id == 56) {
                        contract = new getWeb3.eth.Contract(contractABI, contractAddress);
                        getWeb3.eth.getBalance(accounts[0], function (err, res) {
                            if (!err) {
                                
                                callback(contract, accounts[0], res);
                            } else {
                                console.log(err);
                            }
                        })
                    } 
                });
            })
        }

}
//导出相应的方法
export default {
    Init
}


