          <li @click="changePlan(3)">套餐四:500+价值50U的{{type == 'hrod'?'HROD':'ROD'}}，封顶收益600U</li>
          <li @click="changePlan(4)">套餐五:2000U+价值100U的{{type == 'hrod'?'HROD':'ROD'}}，封顶收益2400U</li>
          <li @click="changePlan(5)">套餐六:10000U+价值1000U的{{type == 'hrod'?'HROD':'ROD'}}，封顶收益13000U</li>
          <li @click="changePlan(6)">套餐七:50000U+价值2000U的{{type == 'hrod'?'HROD':'ROD'}}，封顶收益55000U</li>
