add_DaoContract:({ commit }, data) => {
  var _this = this;
  let DaoInfo = {};
  getWeb3.Init(pool_dao, '0x181bcc32d5dc33025f50e108f18543b0dcc23395', (contract, addr, balance) => {
    commit('set_DaoContract', contract);
    // commit('set_balance', web3.utils.fromWei(balance, 'ether'));
    DaoInfo.address = '0x181bcc32d5dc33025f50e108f18543b0dcc23395';
    contract.methods.getUserInfo(addr,0,0).call().then((res) => {
     
      DaoInfo.info = res[0];
      DaoInfo.inviteCode = res[1];
      DaoInfo.referrer = res[2];
     
    }).then(()=>{
      // AES单价
      contract.methods.getTokenPrice(Vue.prototype.ArrList.AesArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call()
      .then((data) => {
        var prex =4;
        var price = web3.utils.fromWei(data, 'ether');
        DaoInfo.AESPrice =null;
        if (parseInt(price) == price){
          DaoInfo.AESPrice=price;
          // console.log(info)
                commit('set_DaoInfo', DaoInfo);
          return 
        } 
        let len = 1;
        let i = 0;
        while (parseInt(price) != price) {
          price = price * 10;
            len *= 10;
            i++;
        }
        let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
        let d1 = price - last;
        if (last > 0) {
            d1 = d1 + Math.pow(10, (i - prex));
        }
        d1 = d1 / len;
        DaoInfo.AESPrice=d1;
    
      })
    }).then(()=>{
      // F5单价
      contract.methods.getTokenPriceMultihop(Vue.prototype.ArrList.AesArr,Vue.prototype.ArrList.FiveArr,'1000000000000000000').call()
      .then((data) => {
        var prex =4;
        var price = web3.utils.fromWei(data, 'ether');
        DaoInfo.f5Price =null;
        if (parseInt(price) == price){
          DaoInfo.f5Price=price;
          // console.log(info)
                commit('set_DaoInfo', DaoInfo);
          return 
        } 
        let len = 1;
        let i = 0;
        while (parseInt(price) != price) {
          price = price * 10;
            len *= 10;
            i++;
        }
        let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
        let d1 = price - last;
        if (last > 0) {
            d1 = d1 + Math.pow(10, (i - prex));
        }
        d1 = d1 / len;
        DaoInfo.f5Price=d1;
    
      })
    }).then(()=>{
      contract.methods.getGameInfo().call().then((res3) => {
        DaoInfo.qwInvestMoney=res3[4];
       
      })
    }).then(()=>{
      contract.methods.unbalancedStaticProfit(addr).call().then((res4) => {
        console.log(res4)
        // return
        DaoInfo.allStaticAmount=res4;
        console.log('set_DaoInfo=============',DaoInfo)
       commit('set_DaoInfo', DaoInfo);
       
      })
    })
})
},

