<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      isShowNav: false,
      showRule: false,
      show: false,
      lang_1:null,
      langList: [
        {
          text: "中文",
          img_txt: "zhCHS",
        },
        {
          text: "EN",
          img_txt: "en",
        },
      ],
    };
  },
  props: {
    lang: {
      type: String,
      default: "zhCHS",
    },
     page1: {
      type: String,
      default: "home",
    },
    type: {
      type: String,
      default: "home",
    },
     titleHead: {
      type: String,
      default: " ",
    },
    isHome: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("main", ["account", "oktInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_oktInfo"]),
    showPopup() {
      this.show = true;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.$emit("changeAppLocale", locale);
      // this.isShowNav = !this.isShowNav;
    },
    LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
    tips() {
      this.$toast.fail(this.$t("message.tips3"));
      // this.$dialog.alert({
      //   message: this.$t("message.tips3"),
      //   theme: "round-button",
      // });
    },
    prev() {
      this.$router.go(-1);
    },

    // 	showPopup() {
    //   this.showRule = true;
    // },
    hreftwo(url) {
      // console.log(url.length);
      // if (url.length > 1) {
      //   this.$router.push({ path: url + "/" + this.lang });
      // } else {
        if(url.length>2){
          this.$router.push({ path: url + "/?invite=" + this.$route.query.invite  });
        }else{
          this.$router.push({ path: url });
        }
      
      // }
    },
  },
};
</script>

<style scoped lang="less">
#header {
  padding: 20px 3% 10px;
  position: absolute;
  top: 0;
  width: 100%;
  box-sizing: border-box;
}
.accountBox {
  padding: 8px 30px;
     background: url(../assets/img/mune/bg.png)bottom center no-repeat;
    background-size: 100% 100%;
  border-radius: 6px;
  opacity: 0.8;
  margin: 20px 0;
  p {
    margin-left: 10px;
    height: 25px;
    line-height: 25px;
    color: #FFFFFF;
  }
}
.flex {
  display: flex;
}
.bg_logo {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 600;
  color: #2DF0AE;
  // background: #f4f7fe;
  border-radius: 4px;
  // border: 1px solid #e1e1e1;
  padding: 0 4px;
  img {
    width: 20px;
    vertical-align: sub;
  }
}
.menu {
  padding: 30px 20px;
  position: relative;
  height: 90%;
  text-align: center;
  .list {
    // border-top: 2px solid #f4f7fe;
    margin-top: 20px;
    padding-top: 10px;
    img {
      vertical-align: bottom;
      float: left;
      margin-top: 13px;
    }
    a {
      font-size: 15px;
      font-family: PingFang SC;
      font-weight: 500;
      color:#2DF0AE;
      display: block;
      padding: 12px 20px 12px 30px
    }
  }
  .li_col.on{
  background:#0d2f2e;
  a{color:#fff;}
  }
  .li_col{
      border-radius: 10px;
      padding: 0 10px;
      margin: 3px 0;
  }
   .li_col li.in{color:#FFB237;}
  .li_col li{
    padding: 5px 26px;
    color: #999;
    .gou{
          width: 6px;
    float: none;
    margin-top: 0;
    margin-right: 5px;
    vertical-align: middle;
    }
  }
  .foot {
    position: absolute;
    bottom: 20px;
    img {
      display: inline-block;
      margin-right: 8px;
    }
  }
}
</style>



